.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login >p{
    margin-top: 10px;
    color: darkgray;
}

.login-logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}

.login-container{
    width: 350px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login-container>h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login-container > form > h5{
    font-size: 14px;
    margin-bottom: 5px;
}
.login-container> form >input{
    height: 30px;
    margin-bottom: 10px;
    background-color: #fff;
    width: 98%;
}

.login-container > p{
    margin-top: 15px;
    font-size: 12px;
}

.login-signIn{
    /* background: #f0c14b; */
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    color: #111;
    /* border-color: #a88734 #9c7e31 #846a29; */
    background-color: #5895cb;
    border-color: #69a7dd #6f90ad #87c4f8;
    cursor: pointer;
}

.login-register{
    border-radius: 2px;
    width: 390px;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
    font-size: 12.5px;
    cursor: pointer;
}